.login {
  max-width: 1200px;
  position: relative;
  margin: 20px auto;
}

.login img {
  width: 100%;
  height: 400px;
}

.login .form {
  position: absolute;
  top: 20px;
  left: 20%;
  z-index: 15;
  width: 400px;
  height: 350px;
  border: 3px solid rgba(0, 0, 0, 0.285);
  border-radius: 10px;
  box-shadow: 3px 3px 5px 5px rgba(0, 0, 0, 0.134);
  display: flex;
  flex-direction: column;
  align-items: center;
}

.login .form input {
  width: 90%;
  margin-bottom: 20px;
  padding: 5px 10px;
  font-size: 18px;
}

.login .form button {
  color: #fff;
  background-color: #333;
  padding: 10px 30px;
  border-radius: 5px;
  font-size: 16px;
  display: block;
  margin: 20px 0;
}

.login .form button:hover {
  cursor: pointer;
}

.login .form h1 {
  margin: 20px 0;
}

@media (max-width: 768px) {
  .login img {
    display: none;
  }

  .login .form {
    position: relative;
    margin-bottom: 30px;
    top: 0;
    left: 50%;
    transform: translate(-50%);
  }
}
